/* Pastel Orange Theme */
body, .excel-processor {
  background: #f5f5f5;
}

.titer-header {
  text-align: center;
  margin: 2rem 0 1.5rem 0;
  font-size: 2.2rem;
  color: #d97706;
  font-weight: bold;
  letter-spacing: 0.02em;
}

/* Instructions Card */
.instructions-card {
  background: #fff1de;
  border: 1px solid #ffd6a0;
  border-radius: 12px;
  max-width: 700px;
  margin: 0 auto 2rem auto;
  padding: 1.5rem 2rem;
  box-shadow: 0 2px 8px rgba(255, 183, 77, 0.08);
  color: #a15c00;
}
.instructions-card h2 {
  margin-top: 0;
  color: #d97706;
  font-size: 1.3rem;
  font-weight: bold;
}
.instructions-card ol {
  margin: 0;
  padding-left: 1.2em;
}
.instructions-card li {
  margin-bottom: 0.5em;
  font-size: 1.05rem;
}

/* Controls & Upload */
.upload-section {
  background: #fff3e0;
  border-radius: 10px;
  padding: 1.5rem;
  margin-bottom: 2rem;
  border: 1px solid #ffd6a0;
  box-shadow: 0 1px 4px rgba(255, 183, 77, 0.07);
}

.file-controls {
  background: #fff7f0;
  border-radius: 8px;
  padding: 1rem;
  box-shadow: 0 1px 4px rgba(255, 183, 77, 0.05);
  border: 1px solid #ffe0b2;
  justify-content: center;
}

/* Buttons */
.download-button,
.save-button,
.cancel-button,
.print-button {
  background: #ffb74d;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 0.5rem 1.2rem;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s, box-shadow 0.2s;
  box-shadow: 0 1px 3px rgba(255, 183, 77, 0.08);
}
.download-button:hover,
.save-button:hover,
.print-button:hover {
  background: #ffa726;
}
.cancel-button {
  background: #ff7043;
}
.cancel-button:hover {
  background: #f4511e;
}

/* Table & Card Styling */
.summary-card,
.sheet-summary,
.preview-table-titer,
.percent-cv-card,
.card-header {
  background: #ffffff;
  border: 1px solid #ffd6a0;
  border-radius: 10px;
  box-shadow: 0 1px 6px rgba(255, 183, 77, 0.07);
  margin-bottom: 1.5rem;
  padding: 1.2rem;
  
}

.summary-card h2,
.card-title h2 {
  color: #d97706;
}

.preview-table-titer th,
th {
  background: #ffe5c2;
  color: #a15c00;
}

.preview-table-titer th,
.preview-table-titer td {
  padding: 4px 6px;
  border: 1px solid #ddd;
  text-align: left;
}

.preview-table-titer tr:nth-child(even),
tr:nth-child(even) {
  background: #fff3e0;
}

.preview-table-titer td, .preview-table-titer
td {
  background: #fff8f2;
  color: #a15c00;
  font-size: 1.2vw;
}

.preview-table-titer td:hover, .preview-table-titer
tr:hover td {
  background: #ffffff;
}

.highlight-yellow { background: #ffeeac !important; }
.highlight-green { background: #cbf1c7 !important; }

.excluded-cell {
  background: #ffb3b3 !important; /* Brighter pastel red for excluded cells */
  color: #b22222 !important;      /* Stronger red text for visibility */
  text-decoration: line-through;
  font-weight: bold;
  border: 2px solid #ff7043 !important; /* Orange-red border for extra emphasis */
  box-shadow: 0 0 0 2px #ffd6a0;
}


/* Sticky Tabs */
.sheet-tabs-sticky {
  position: sticky;
  top: 4.93rem;
  z-index: 1000;
  background: #fff7f0;
  padding: 8px 0 4px 0;
  border-bottom: 2px solid #ffd6a0;
  border-radius: 0 0 8px 8px

}

button.sheet-tab {
  background: #ffe0b2;           /* Pastel orange */
  color: #a15c00;
  border: 1px solid #ffd6a0;
  border-radius: 8px 8px 0 0;
  /* padding: 0.5rem 1.2rem; */
  margin-right: 0.5rem;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
}

/* Misc */
.loading {
  color: #d97706;
  font-style: italic;
  text-align: center;
  margin: 1.5rem 0;
}

.sheet-tab {
  background: #d4edda;           /* Pastel green */
  color: #256029;
  border: 1px solid #b7e4c7;
  border-radius: 8px 8px 0 0;
  padding: 0.5rem 1.2rem;
  margin-right: 0.5rem;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
  box-shadow: 0 1px 3px rgba(44, 130, 87, 0.07);
  padding-right: 3em; /* Add extra space for the edit icon */
  position: relative;
  overflow: hidden;
}

.sheet-tab .edit-button {
  position: absolute;
  right: 0.7em;
  top: 50%;
  transform: translateY(-50%);
  margin-left: 0;
  background: none;
  border: none;
  color: #888;
  cursor: pointer;
  font-size: 0.9em;
  opacity: 0.7;
  transition: opacity 0.2s;
  z-index: 2;
}

.sheet-tab:hover .edit-button {
  opacity: 1;
}

.sheet-tab:hover,
.sheet-tab:focus {
  background: #b7e4c7;
  color: #155724;
}

.sheet-tab.active {
  background: #81c784;
  color: #fff;
  border-bottom: 2px solid #388e3c;
  font-weight: bold;
  z-index: 2;
}

/* Responsive: Match nav bar stacking and alignment */
@media (max-width: 930px) {
  .sheet-tabs-sticky {
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    top: 7.4rem; /* Match your mobile nav bar height if it grows */
    padding: 4px 0 2px 0;
  }

  @media (max-width: 800px) {
    .sheet-tabs-sticky {
      top: 7.4rem; /* Adjust for smaller screens */
    }

    @media (max-width: 768px) {
      .sheet-tabs-sticky {
        top: 20rem; /* Further adjust for very small screens */
      }
    }
  }
  .sheet-tab {
    width: 100%;
    max-width: 300px;
    text-align: center;
    margin-right: 0;
    border-radius: 8px;
    font-size: 1.05rem;
  }
}

@media screen and (min-width: 1600px) {
  .preview-table-titer td, .preview-table-titer
td{
     font-size: 20px;
  }
}